@import "../../style/variables";

.jobList-table {
	font-family: $mainFont;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border-radius: 15px;
	margin: 2em;

	table {
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 15px;
		background-color: $darkAccent;
		border-collapse: collapse;
	}

	#jobRow {
		background-color: $mainColor;
		border-bottom: 2px solid $light;
    color: $light;

		td {
			padding-bottom: 0.5em;
			padding-top: 0.5em;
		}
	}

	#headings {
		display: flex;
		justify-content: space-around;
		color: $light;
		font-size: 1.2em;
		font-weight: bold;
	}
}

h2 {
	font-family: $headerFont;
	color: $light;
	font-size: 40px;
	text-shadow: 4px 4px #00000046;
}

@media (min-width: 720px){
  .jobList-table {
    table {
      width: 100%;
      tbody {
        width: 100%;
      }
    }
    #jobRow {
      display: flex;
      justify-content: space-around;
      text-align: left;
      align-items: center;
    }
  }
}