body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#formDiv{display:flex;flex-direction:column;border-radius:15px;margin:2em;background-color:#6DAFFE;width:75%}#formDiv h3{font-family:"Fredoka One",cursive;color:white;font-size:30px;text-shadow:4px 4px #00000046}#formDiv .jobForm{font-family:"Varela Round",sans-serif;display:flex;margin:2em;margin-top:0.5em;flex-direction:column;align-items:center}#formDiv .jobForm label{display:flex;flex-direction:column;width:100%;font-size:20px;margin-bottom:1.5em;color:white}#formDiv .jobForm input{height:2.5em}#formDiv .jobForm #addJobButton{font-family:"Varela Round",sans-serif;cursor:pointer;color:#6DAFFE;font-size:16px;text-transform:uppercase;width:50%;text-align:center;border:0;padding:10px 0;border-radius:5px}#formDiv .jobForm #addJobButton:hover{background-color:#437FC7;color:white}

.myjob-table{font-family:"Varela Round",sans-serif;display:flex;flex-direction:column;justify-content:flex-start;border-radius:15px;margin:2em;background-color:#6DAFFE;width:75%}.myjob-table table{margin:1em;width:95%;border-collapse:collapse;border-radius:15px;border-spacing:20px 20px}.myjob-table #jobRow td{color:white;padding-bottom:0.5em;padding-top:0.5em}.myjob-table #headings{color:white;font-size:1.25em;font-weight:bold}.myjob-table h3{font-family:"Fredoka One",cursive;color:white;font-size:30px;text-shadow:4px 4px #00000046}

.dashBoardDiv{display:flex;flex-direction:column;justify-content:center;align-items:center}@media (min-width: 720px){.dashBoardDiv{flex-direction:row;align-items:flex-start}}

header{background-image:linear-gradient(to top, #437FC7 0%, #6DAFFE 100%)}header h1{font-family:"Fredoka One",cursive;font-size:50px;color:#eee;display:inline-block;text-shadow:4px 4px #00000046}header h4{margin:0;margin-right:4em;text-align:right;color:#F9F9F9}

.modal{bottom:0;left:0;position:absolute;right:0;top:0;z-index:100}.modal:after{background:#000;bottom:0;content:" ";left:0;opacity:0.5;position:absolute;right:0;top:0}.modal .modal-window{color:white;font-family:"Varela Round",sans-serif;font-size:1em;background-color:#6DAFFE}.modal .modal-interior{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:5px}.modal #modal-btn-container{display:flex;justify-content:center;align-items:center}.modal #modal-button{font-family:"Varela Round",sans-serif;cursor:pointer;color:#6DAFFE;font-size:16px;width:50%;text-transform:uppercase;text-align:center;border:0;padding:10px 0;border-radius:5px}.modal #modal-button:hover{background-color:#437FC7;color:white}.modal>div{box-shadow:0 1px 4px rgba(0,0,0,0.3),0 0 40px rgba(0,0,0,0.1) inset;box-sizing:border-box;border-radius:5px;display:flex;flex-direction:column;justify-content:center;text-align:left;height:40%;margin:auto;padding:0;position:relative;top:50%;transform:translateY(-50%);width:85%;z-index:1000}.modal>div>header{background:#437FC7;border-bottom:0 none;border-top-left-radius:5px;border-top-right-radius:5px;box-shadow:0 1px 5px rgba(0,0,0,0.46);display:flex;font-size:1em;font-weight:bold;height:1.5em;line-height:1.5em;padding:0.25em 1em}.modal>div>header span{color:white;flex:1 1}.modal>div>header button{background:inherit;border:0;cursor:pointer;order:1;text-align:center;font-size:1.4em;font-weight:bold;color:#fff;margin-top:2px;transition:0.3s ease-in}.modal>div>header button:hover{transform:scale(0.7)}.modal>div>div{flex:1 1;overflow:auto;padding:25px}@media (min-width: 720px){.modal>div{width:65%;height:35%}}

.modal-container{display:flex;flex-direction:column}#description-container{display:flex;flex-direction:row;justify-content:space-between}#description-1{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;align-content:flex-start}#description-2{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;align-content:flex-start}label{margin-top:10px;margin-bottom:3px}span{padding:2px}button:hover{cursor:pointer}p{font-size:14px}#modal-button{width:75px;margin-top:10px}

.jobList-table{font-family:"Varela Round",sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:15px;margin:2em}.jobList-table table{display:flex;justify-content:center;align-items:center;border-radius:15px;background-color:#6DAFFE;border-collapse:collapse}.jobList-table #jobRow{background-color:#437FC7;border-bottom:2px solid #F9F9F9;color:#F9F9F9}.jobList-table #jobRow td{padding-bottom:0.5em;padding-top:0.5em}.jobList-table #headings{display:flex;justify-content:space-around;color:#F9F9F9;font-size:1.2em;font-weight:bold}h2{font-family:"Fredoka One",cursive;color:#F9F9F9;font-size:40px;text-shadow:4px 4px #00000046}@media (min-width: 720px){.jobList-table table{width:100%}.jobList-table table tbody{width:100%}.jobList-table #jobRow{display:flex;justify-content:space-around;text-align:left;align-items:center}}

#nav{font-family:"Fredoka One",cursive;display:flex;justify-content:center;align-items:center;background:#437FC7;box-shadow:5px 5px 5px rgba(52,52,69,0.3)}#nav ul{list-style-type:none;margin:0;padding:0}#nav ul li{display:inline-block;padding:0.5rem 2rem;position:relative}#nav ul li span{color:white;text-decoration:none}#nav ul li span:after{bottom:0;content:"";left:0;position:absolute;right:0;top:0}#nav ul li:hover{background:#6DAFFE;cursor:pointer}

.signInDiv{display:flex;width:70%;flex-direction:column;justify-content:end}.signInForm{background-image:linear-gradient(to top, #6DAFFE 0%, #437FC7 100%);padding:30px 30px 30px 30px;border-radius:10px;top:50%;left:50%;width:400px}.signInForm input{font-family:"PT Mono",monospace;border-radius:5px;font-size:16px;background:#F9F9F9;width:100%;border:0;padding:10px 10px;margin:15px -10px}#loginButton{font-family:"Varela Round",sans-serif;cursor:pointer;color:#6DAFFE;font-size:16px;text-transform:uppercase;width:50%;text-align:center;border:0;padding:10px 0;border-radius:5px}#loginButton:hover{background-color:#437FC7;color:white}.loginSwitch{display:inline-block;transition-duration:0.3s;transition-property:transform;-webkit-tap-highlight-color:transparent;transform:translateZ(0);box-shadow:0 0 1px transparent}.loginSwitch:hover{transform:scale(1.1)}

#signUpDiv{display:flex;width:70%;flex-direction:column;justify-content:center;align-items:center}.signUpForm{background-image:linear-gradient(to top, #6DAFFE 0%, #437FC7 100%);padding:30px 30px 30px 30px;border-radius:10px;top:50%;left:50%;width:400px;box-shadow:5px 10px 10px rgba(52,52,69,0.2)}.signUpForm input{font-family:"PT Mono",monospace;border-radius:5px;font-size:16px;background:#F9F9F9;width:100%;border:0;padding:10px 10px;margin:15px -10px}#loginButton{font-family:"Varela Round",sans-serif;cursor:pointer;color:#6DAFFE;font-size:16px;text-transform:uppercase;width:50%;text-align:center;border:0;padding:10px 0;border-radius:5px}

h1{font-family:"Varela Round",sans-serif}ul{list-style-type:none;text-align:left}ul li{padding:5px}p{width:500px;font-size:20px}.splashDiv div{display:flex;justify-content:center;align-items:center}@media (min-width: 700px){.splashDiv{display:flex;flex-direction:column;justify-content:center;align-items:center}}


.App {
  text-align: center;
  background-color: #F9F9F9;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

